      <div class="contact__wrap">
        <div class="contact__form">
          <div>
            <label for="">Name<span>*</span></label>
            <input v-model="name" type="text">
          </div>
          <div>
            <label for="">Email<span>*</span></label>
            <input v-model="email" type="text">
          </div>
          <div>
            <label for="">Subject<span>*</span></label>
            <input v-model="subject" type="text">
          </div>
          <div>
            <label for="">Message<span>*</span></label>
            <textarea v-model="msg"></textarea>
          </div>
          <div class='contact__btn-wrap'>
            <div></div>
            <div><button @click="submit" class='contact__btn'>submit</button></div>
          </div>
        </div>
        <div class="contact__address">
          <h5>Address and Phone</h5>
          <p>Emir Dzhetybaev</p>
          <p>Bishkek</p>
          <p>Chuy Region</p>
          <p>+996755301525</p>
          <p><span><a href="mailto: dzhetybaev19emir@gmail.com">dzhetybaev19emir@gmail.com</a></span></p>
        </div>
      </div>
    
      <transition name='slide'>
        <div 
          v-if="modalShown.state" 
          class="modal-window" 
          :class="{
            'error': modalShown.type === 'error', 
            'success': modalShown.type === 'success'
          }"
        >
          <h1>{{ modalShown.text }}</h1>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      subject: '',
      email: '',
      msg: '',
      modalShown: {
        state: false,
        type: '',
        text: ''
      },
    }
  },
  methods: {
    submit() {
      if (this.name === '' || this.subject === '' || this.email === '' || this.msg === '' ) {
        let newState = { // modal new state
          state: true,
          type: 'error',
          text: 'Fill all fields'
        }
        this.modalShown = newState;
        setTimeout(() => { // close modal
          this.modalShown = false;
        }, 2000)
        return
      } 
      this.modalShown = {
        state: true,
        type: 'success',
        text: 'Success'
      };
      setTimeout(() => {
        this.modalShown = false;
      }, 2000)
    }
  },

  mounted() {
    window.addEventListener('load', () => {
      this.$store.commit('setContactTopEdge', this.$refs.contact.offsetTop)
    })

    window.addEventListener('resize', () => {
      this.$store.commit('setContactTopEdge', this.$refs.contact.offsetTop)
    })
    
  }
}
</script>

