<template>
  <div ref='home' class="home">
    <!----- desktop navbar ----->
    <transition name='fade'>
      <div :class="{'dark' : dark}" v-if="seen" ref='nav' class="nav">
        <ul class="nav__list">
          <li @click="scroll(0)" :class="{active: active === 1}" class='nav__item'><button>Home</button></li>
          <li @click="scroll($store.state.aboutTopEdge)" :class="{active: active === 2}" class='nav__item'><button>About</button></li>
          <li @click="scroll($store.state.resumeTopEdge)" :class="{active: active === 3}" class='nav__item'><button>Stack</button></li>
          <li @click="scroll($store.state.worksTopEdge)" :class="{active: active === 4}" class='nav__item'><button>Works</button></li>
          <!-- <li @click="scroll($store.state.feedbackTopEdge)" :class="{active: active === 5}" class='nav__item'><button>Testimonials</button></li> -->
          <li @click="scroll($store.state.contactTopEdge)" :class="{active: active === 6}" class='nav__item'><button>Contact</button></li>
        </ul>
      </div>
    </transition>
    <!-------------------------------------->


    <!-- <transition-group name='list-complete' tag='div'>   -->
      <transition name='fade-sidebar'>
        <div key="1" v-if="!sideBar" @click="showSideBar" class="hambuger__menu">
          <i class="fas fa-bars fa-3x"></i>
        </div>
      </transition>
      <transition name='fade-sidebar'>
        <div key="2" v-if="sideBar" @click="sideBar = !sideBar" class="hambuger__menu">
          <i class="fas fa-times fa-3x"></i>
        </div>
      </transition>
    <!-- </transition-group> -->



    <!-- mobile navbar(sidebar) -->

    <transition name="slide">
      <nav ref='nav' v-if="sideBar" class="sidebar">
        <ul class="nav__list">
          <li @click="scroll(0)" :class="{active: active === 1}" class='nav__item'><button>Home</button></li>
          <li @click="scroll($store.state.aboutTopEdge)" :class="{active: active === 2}" class='nav__item'><button>About</button></li>
          <li @click="scroll($store.state.resumeTopEdge)" :class="{active: active === 3}" class='nav__item'><button>Resume</button></li>
          <li @click="scroll($store.state.worksTopEdge)" :class="{active: active === 4}" class='nav__item'><button>Works</button></li>
          <!-- <li @click="scroll($store.state.feedbackTopEdge)" :class="{active: active === 5}" class='nav__item'><button>Testimonials</button></li> -->
          <li @click="scroll($store.state.contactTopEdge)" :class="{active: active === 6}" class='nav__item'><button>Contact</button></li>
        </ul>
      </nav>
    </transition>
    
    <transition name='fade'>
      <div v-if="sideBar" class="overlay"></div>
    </transition>

    <!-------------------------------------->


    <div ref='test' class="home__wrap">

      <div ref='title' class="title col-md-11">
        <h1>I'm Emir Dzhetybaev.</h1>
        <h3>
          I'm <span>Bishkek</span> based <span>Front End Engineer</span>, currently doing an internship in <span>Ooba.kg</span> where I work as a <span>front end developer across the range of our products</span>.
        </h3>
      </div>

      <div @click='scroll($store.state.aboutTopEdge)'>
        <i class="btn-down fas fa-chevron-circle-down fa-3x"></i>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data() {
    return {
      seen: true,
      dark: false,
      active: 1,
      sideBar: false
    }
  },
  methods: {
    scroll(target) {
      window.scrollTo({top: target, behavior: 'smooth'})
      this.sideBar = false;
    },
    getSizes() {
      this.$store.commit('setNavHeight', this.$refs.nav.scrollHeight)
      this.$store.commit('setTitleTopEdge', this.$refs.title.offsetTop)
      this.$store.commit('setHomeTopEdge', this.$refs.home.offsetTop)
    },
    setActiveNavItem() {
      this.active = 1;
      if (window.scrollY + this.$store.state.navHeight >= this.$store.state.homeTopEdge) {
        this.active = 1;
        this.dark = false
      }
      if (window.scrollY + this.$store.state.navHeight >= this.$store.state.aboutTopEdge) {
        this.active = 2;
        this.dark = true
      }
      if (window.scrollY + this.$store.state.navHeight >= this.$store.state.resumeTopEdge) {
        this.active = 3;
      }
      if (window.scrollY + this.$store.state.navHeight >= this.$store.state.worksTopEdge) {
        this.active = 4;
      }
      // if (window.scrollY + this.$store.state.navHeight >= this.$store.state.feedbackTopEdge) {
      //   this.active = 5;
      // }
      if (window.scrollY + this.$store.state.navHeight >= this.$store.state.contactTopEdge) {
        this.active = 6;
      }
    },
    showSideBar() {
      this.sideBar = !this.sideBar
    }
  },
  watch: {
    sideBar: function() {
      this.sideBar ? document.body.style.overflow = 'hidden' : document.body.style.overflow = ''
    }
  },
  mounted() {
    this.getSizes()
    this.setActiveNavItem()
    this.active = 1;
    this.dark = false;

    window.addEventListener('resize', () => {
      this.getSizes()
    })
    window.addEventListener('scroll', () => {
      this.setActiveNavItem()
    })
  },
}
</script>

